import React from "react";

export default function picture(props) {
  const { imgSrc, altSrc } = props;
  return (
    <picture>
      <source srcSet={imgSrc} type="image/webp" />
      <source srcSet={altSrc} type="image/png" />
      <img src={imgSrc} className="img-responsive" />
    </picture>
  );
}
